<template>
  <section class="text-center my-5">
    <slick class="" :options="slickOptions">

      <div class="px-0 px-md-5">
        <div class="custom_circle text-left">
          <div class="custom_circle__box">
            <img class="custom_circle__img" src="src/assets/yacht.jpg" alt="yacht">
          </div>
          <h4 class="custom_circle__ribbon custom_circle__ribbon--name">Miniviking</h4>
