    </slick>
  </section>
</template>

<script>
  import Slick from 'vue-slick';
  export default {
    name: 'yachtsComponent',
    data() {
      return {
        slickOptions: {
          slidesToShow: 3,
          arrows: true,
          // autoplay: true,
          responsive: [{
              breakpoint: 1100,
              settings: {
                slidesToShow: 2
              }
            },
            {
              breakpoint: 576,
              settings: {
                arrows: false,
                slidesToShow: 1
              }
            }
          ]
        },

      }
    },
    methods: {
      next() {
        this.$refs.slick.next();
      },

      prev() {
        this.$refs.slick.prev();
      },

      reInit() {
        // Helpful if you have to deal with v-for to update dynamic lists
        this.$nextTick(() => {
          this.$refs.slick.reSlick();
        });
      }
    },
    components: {
      Slick,
    },
  }
</script>

<style lang="scss">
  @import "../../node_modules/bootstrap/scss/functions";
  @import '../../src/scss/_variables';
  @import "../../node_modules/bootstrap/scss/mixins";
  @import "../../node_modules/slick-carousel/slick/slick.css";
  @import "../../src/scss/_slick-theme";
  @import "../../src/scss/_square";
</style>


